/*### Big Screen CSS Here ### */

.background-image-home {
  background-image: url(../../public/Image-background.png);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.overlay-image-home {
  background-image: url(../../public/Overlay.png);
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.text-container-home {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.text-container-hero {
  margin-left: 98px !important;
}

.subtitle-container-hero {
  margin-left: 290px !important;
  margin-top: "52px" !important;
}

.text-title-hero {
  color: #41d2bf !important;
  font-weight: 700 !important;
  font-size: 70px !important;
  letter-spacing: -0.05em !important;
  line-height: 70px !important;
}

.text-subtitle-hero {
  color: #fefefe !important;
  font-weight: 700 !important;
  font-size: 70px !important;
  letter-spacing: -0.05em !important;
  line-height: 70px !important;
}

.text-slogan-hero {
  color: #fefefe !important;
  font-weight: 300 !important;
  letter-spacing: 0.1em !important;
  line-height: 31px !important;
  font-size: 25px !important;
  padding-right: 20px !important;
  margin-top: 20px !important;
}

/*### Desktop Screen CSS Here ### */
@media screen and (max-width: 1200px) {
  .text-container-hero {
    margin-left: 90px !important;
  }

  .subtitle-container-hero {
    margin-left: 282px !important;
  }

  .text-title-hero {
    font-size: 60px !important;
    line-height: 60px !important;
  }

  .text-subtitle-hero {
    font-size: 60px !important;
    line-height: 60px !important;
  }

  .text-slogan-hero {
    line-height: 28px !important;
    font-size: 22px !important;
    padding-right: 18px;
  }
}

/*### Laptop Screen CSS Here ### */
@media screen and (max-width: 1024px) {
  .subtitle-container-hero {
    margin-left: 180px !important;
    margin-top: 20px !important;
  }
}

/*### Tablet Screen CSS Here ### */
@media screen and (max-width: 768px) {
  .text-container-hero {
    margin-left: 30px !important;
  }

  .subtitle-container-hero {
    margin-left: 80px !important;
    margin-top: 20px !important;
  }

  .text-title-hero {
    font-size: 50px !important;
    line-height: 50px !important;
  }

  .text-subtitle-hero {
    font-size: 50px !important;
    line-height: 50px !important;
  }

  .text-slogan-hero {
    font-size: 20px !important;
  }
}

/*### Phone Screen CSS Here ### */
@media screen and (max-width: 480px) {
  .text-container-hero {
    margin-left: 20px !important;
  }

  .subtitle-container-hero {
    margin-left: 80px !important;
    margin-top: 20px !important;
  }

  .text-title-hero {
    font-size: 40px !important;
    line-height: 40px !important;
  }

  .text-subtitle-hero {
    font-size: 40px !important;
    line-height: 40px !important;
  }
}
